export default {
	data () {
		return {
			userInfo: {}
		}
	},
	onShow () {
		this.getUserInfo()
	},
	methods: {
		// 获取用户信息
		getUserInfo () {
			this.$api.userInfo({}, res => {
				if (res.status) {
					this.userInfo = res.data
				} else {
					this.$common.errorToShow(res.msg)
				}
			})
		},
		// 页面跳转
		navigateToHandle (pageUrl) {
			this.$common.navigateTo(pageUrl)
		}
	}
}	
